<template>
    <div class="page approve-details">
        <div class="approve-details-content bg">
            <h2>{{$t('details.title')}}</h2>
            <ul>
                <li>
                    <p class="key">{{$t('details.domain')}}</p>
                    <p class="value">{{details.domain}}</p>
                </li>
                <li>
                    <p class="key">{{$t('details.type')}}</p>
                    <p class="value">{{$t('details.company')}}</p>
                </li>
                <li>
                    <p class="key">{{$t('details.companyName')}}</p>
                    <p class="value">{{details.name}}</p>
                </li>
                <li>
                    <p class="key">{{$t('details.creditCode')}}</p>
                    <p class="value">{{details.code}}</p>
                </li>
                <li>
                    <p class="key">{{$t('details.officialWebsite')}}</p>
                    <p class="value">{{details.officeWeb}}</p>
                </li>
                <li class="block">
                    <p class="key">{{$t('details.details')}}</p>
                    <p class="value wrap">{{details.description}}</p>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import { publicPost } from '../../js/api/index.js';
    import getApiUrl from '../../js/api/apiUrlList';

    export default {
        name: 'approve-details',
        data () {
            return {
                details: {},
                userId: ''
            };
        },
        created () {
            this.$router.setTitle(this.$t('title.approveDetails'));
            this.getInfos();
        },
        methods: {
            getInfos () {
                let params = {},
                    self = this;
                params.userId = self.$route.query.userId;
                publicPost(getApiUrl('getTrusted'), params).then((res) => {
                    self.details = res;
                }).catch((e) => {
                    self.$toast.center(e);
                });
            }
        }
    };
</script>

